<template>
  <div class="new_song">
   <ul>
      <li v-for="(item,index) in newsong" @mouseenter="enter(index)" @mouseleave="leave()" @click="pl(item)">
        <div class="order" v-if="plays&&current==index">{{index+1}}</div>
        <div class="order" v-else><img src="../../../assets/images/icon/play.png" alt=""></div>
        <div class="new_song_img">
          <img v-bind:src="item.picUrl" alt="" @click="plays">
        </div>
      <div class="newsong_text">
          <h3>{{item.name}}</h3>
          <p>{{item.song.artists[0].name}}</p>
        </div>

        <div class="new_song_duration">{{(item.song.bMusic.playTime-(item.song.bMusic.playTime%(1000*60)))/60000}}:{{parseInt((item.song.bMusic.playTime%(1000*60))/1000)}}</div>
      </li>

    </ul>
  </div>
</template>

<script>
  import newsong from '../data/newsong.js'

  export default{
    data(){
      return {
        newsong:newsong.result,
        asss:'我是数据源',
        plays:true,
        current:0
      }
    },
    components:{

    },
    computed:{

    },
    methods:{
      getFullName:function(){
        console.log("hello")
      },
      // play(){
      //   this.$root.vm.$emit('data-index',this.asss)
      //   // console.log('hello')
      // },
      enter(index){
        this.plays=true
        this.current=index
        // console.log(this.play)
      },
      leave(){
        this.plays=false
        // console.log(this.play)
      },
      pl(item){
        this.$emit("playmusic",item)
        // console.log(item)
      }


    }
  }
  // var arr[6]=null
  console.log(newsong)

</script>

<style>
  .new_song{
    margin: 0 auto;
  }
  .new_song_img {
    display: flex;
    align-items: center;
  }
  .new_song_img img{
    width: 50px;
    height: 50px;
    align-items: center;
  }
  .new_song ul{
    width: 1300px;
    height: auto;
 /*   display: block; */
    display: flex;
    flex-wrap: wrap;


  }
  .new_song li{
    /* flex-wrap: wrap; */
    display: flex;
    flex: 0 0 50%;
    margin-top: 20px;
    float: left;
    list-style: none;
    width: 50px;
    height: 70px;
    background-color: #FFFFFF;
  }

  .order{
    width: 60px;
    height: 70px;
    text-align: center;
    line-height: 70px;
    border-left: 1px solid #ddddde;

  }
  .order img{
    margin-top: 20px;
    width: 20px;
    height: 20px;
  }
  .order:hover{
     cursor: pointer;
  }
  .newsong_text{
   flex-grow: 2;
   text-align: center;
   line-height: 25px;
   font-size: 12px;
   padding-top: 12px;
  }
  .newsong_text h3,p{
    width: 300px;
  }


  .new_song_duration{
    flex-grow:1;
    text-align: center;
    line-height: 70px;
  }


</style>



