<template>
  <div id="app">

<!--    <img src="./assets/logo.png"> -->
    <!-- <router-view/> -->

    <div class="pad_top"></div>


    <navbar></navbar>
   <!-- <router-view></router-view> -->
    <div class="box">
      <div class="banner">
        <ass></ass>
      </div>
      <div class="recommend">
          <div class="recommend_title">
            <h4>推荐歌单</h4>
          </div>
          <div class="">
            <recommend></recommend>
          </div>
      </div>

      <div class="newsong">
        <div class="recommend_title">
          <h4>推荐新歌曲</h4>
        </div>
          <div class="">
            <newsong v-on:playmusic="dt"></newsong>
          </div>
          <!-- <div class=""> -->
            <singer></singer>
          <!-- </div> -->
      </div>

    </div>
    <footers></footers>
    <div class="music">
      <music v-bind:item="item"></music>
      </div>

  </div>

</template>

<script>
  import ass from './components/common/swiper.vue'
  import navbar from './components/private/navbar.vue'
  import recommend from './components/private/recommend/recommend.vue'
  import newsong from './components/private/newsong/newsong.vue'
  import singer from 'components/private/singer/singer.vue'
  import footers from './components/private/footer/footer.vue'
  import music from './components/common/musiccpn/music.vue'

export default {
  name: 'App',
  data(){
    return{
       item:{}
    }

  },

  components:{
      ass,navbar,recommend,newsong,singer,footers,music
  },
  // created(){
  //   this.$on('data-index',data=>{
  //       console.log(data)
  //   })
  // },
  methods:{
    dt(item){
      this.item=item
    }
  },
  mounted(){

  }
}
</script>

<style>
  #app{
    margin: 0 auto;
  }
*{
	padding: 0;
	margin: 0;
	/* border: 1px solid #a1a1a1; */
}
html,body{
/* 	width: 100%;
	height: 100%; */
 /* padding-top: 70px; */
}
.pad_top{
  height: 70px;
}
.box{
  height: 2250px;
  width: 100%;
  background-color: #f7f7f8;
  padding-top: 20px;
/*  margin: 0 auto; */
}
.banner{
  width: 1300px;
  margin: 0 auto;
}
.recommend{
  width: 1300px;
  height: auto;
  background-color: #ffffff;
  margin: 20px auto;
}
.recommend_title{
    width: 100%;
    height: 49px;
    line-height: 49px;
    text-indent: 20px;
}
/* .recommend_title h4{
  width: 120px;
  height: auto;
  border: solid 1px #000000;
} */
.newsong{
  width: 1300px;
  height: 600px;
  margin: 0 auto;
}
.music{
  width: 100%;
  height: 70px;
  position: fixed;
  bottom: 0;
  background-color: #FFFFFF;
}

</style>



