      }
    }

  }
  // console.log(recommend)
</script>

<style>
  .recommend ul{
    display: flex;
    flex-wrap: wrap;
  }
  .recommend_box li{

    /* flex-grow: 1; */
    width: 160px;
    height: 220px;
    list-style: none;
    text-align: center;
    /* float: left; */
  }
  .recommend_icon img{
    width: 130px;
    height: 122px;
  }
  .recommend_text{
    margin: 0 auto;
    width: 130px ;
  }
</style>




