<template>

  <div class="bar_audio" v-show="show">
    <audio @canplay="getDuration" @ended="playend" :src="'https://music.163.com/song/media/outer/url?id='+item.id" autoplay=""  ref="au"></audio>
    <!-- <p @click="boost">{{src}}</p> -->
    <!-- 音乐头像 -->
    <div class="audio_icon"><img :src="item.picUrl" alt=""></div>
    <!-- 歌曲名字 -->
    <div class="audio_name" v-if="item.song">
      <h5>{{item.name}}</h5>
      <p>{{item.song.artists[0].name}}</p>
      </div>
    <!-- 播放控制 -->
    <div class="play">
      <span><img src="../../../assets/images/icon/lastsong.png" alt=""></span>
      <span @click="pause()" v-if="playflag"><img src="../../../assets/images/icon/stop.png" alt=""></span>
      <span @click="play() " v-else><img src="../../../assets/images/icon/pl.png" alt=""></span>
      <span><img src="../../../assets/images/icon/nextsong.png" alt=""></span>
    </div>
    <!-- 进度条 -->

    <div class="boxa">
      <span class="progress_left">{{m<10?'0'+m:m}}:{{s<10?'0'+s:s}}</span>
      <span class="progress_right">{{duration_time}}</span>
    	<div class="progress_bar">
    		<div class="bar_inner">
    			<!-- 填充的 -->
    			<div class="progress" :style="{width:count+'px'}"></div>
    			<!-- 原点 -->
    			<div class="progress-btn" :style="{left:count+'px'}" id="cc"></div>

    		</div>
    	</div>
      <!-- 滑动操作 -->
    	<!-- <div class="overlay" v-on:mousemove="overlay"></div> -->
    </div>
    <!-- 音量控制 -->
    <div class="audio_volume">
      <!-- 单曲音乐播放 -->
      <span v-if="selectplay==0"><img src="../../../assets/images/icon/singlesong.png" alt=""></span>
      <span v-else><img src="../../../assets/images/icon/ramdom.png" alt=""></span>
      <span ><img src="../../../assets/images/icon/lyric.png" alt=""></span>
      <!-- 播放列表 -->
      <span @click="showplaylist" class="posi">
        <img src="../../../assets/images/icon/palyList.png" alt="">
        <div class="playlist" v-show="showpalylistbtn">
           <ul>
             <li v-for="(it,index) in stack.arr" :class="listindex==index?'changecolor':''">{{it.name}}</li>
           </ul>
        </div>
        </span>
     <!-- 音量的调整 -->
      <span @click="showvolume"><img src="../../../assets/images/icon/volume.png" alt="">
        <div v-show="showvolumeo" @click.stop="" class="volume">
          <div class="volume_bar" >
          	<div class="volume_inner">
          		<!-- 填充的 -->
          		<div class="volume_pre" :style="{height:volumemovetop+'px'}"></div>
          		<!-- 原点 -->
          		<div class="volume-btn"  :style="{top:volumemovetop+'px'}" id="cc" @></div>
          	</div>
          </div>
          <!-- 罩层 -->
          <div class="overlay" @mousedown="mdown" @mouseup="mdownleave" @mousemove="volumemove($event)"></div>
        </div>
      </span>
    </div>
  </div>
</template>

<script>
  import msi from '../../varible/global.vue'
  export default{
    data(){
      return {
          src:'h',
          count:msi.count,
          s:msi.s,
          m:msi.m,
          duration:0,//总时长
          duration_time:'',
          width:600,//总宽度
          w:0,
          playflag:false,
          // musicid:0,
          //暂停时保存的数据
          stop_m:0,
          stop_s:0,
          stop_stop:false,
           //定时器
           timer:null,
           show:false,
           //音量按钮被单击
           showvolumeo:false,
           //播放列表按钮被单击
           showpalylistbtn:false,
           volumeflag:false,
           //音量的移动
           volumemovetop:0,
           arr:null,
           stack:{
             arr:[]
           },
           listflag:false,
           //当前歌曲播放的下标
           listindex:0,
           //按什么顺序播放列表歌曲,0是单曲循环，非0是随机播放
           selectplay:0,


      }
    },
    props:{
      item:{}
    },
    computed:{

    },

    methods:{
      //当数据改变时添加歌曲到播放列表
      addlist(){
        //下标
        this.stack.arr[this.listsize()]=this.item
      },
      listsize(){
        return this.stack.arr.length
      },
      //显示播放列表
      showlist(){
        console.log('显示播放列表')
        console.log(this.stack.arr);
      },
      //从播放列表中删除
      //判断添加进播放列表的id是否一样
      isId(id){
        console.log(this.stack.arr.length)
          for(let i=0;i< this.stack.arr.length;i++){
            if(id==this.stack.arr[i].id){
              this.listflag=true
            }
            // console.log("执行了吗"+this.stack.arr[i].id)
          }

      },
      //查找播放列表的下标
      queryindex(){
        return this.stack.arr.findIndex((item)=>{
          return item.id===this.item.id
        })
      },
      //音乐改变时切换音乐
      changemusic(){
        this.listindex=this.queryindex();
      },
      //播放列表顺序播放
      orderPlay(){
        //查找当前播放的下标
        this.item=this.stack.arr[this.queryindex()+1]
      },
      //播放列表下一首歌
      nextplaysong(){
        this.orderPlay()
      },

      //播放列表上一首歌
      lastplaysong(){

      },
      //当歌曲播放完毕
      playend(){
        console.log("当前音乐已经播放完毕，已经为您播放下一首")
        this.nextplaysong()
      },





      //当播放按钮被点击
        pause(){
          var that=this
          this.$refs.au.pause()
          // that.playflag=this.$refs.au.pas.paused
          if(this.$refs.au.paused){
            that.playflag=false
          }else{
             that.playflag=true
          }
          this.stop_s=this.s
          this.stop_m=this.m
          // this.stop_stop=false
          // console.log(this.stop_stop)
          clearInterval(that.timer);
          // that.timer=null;

        },
        //当播放按钮被点击
        play(){

          var that=this
          this.$refs.au.play()
          if(this.$refs.au.paused){
            that.playflag=false
          }else{
             that.playflag=true
          }
          this.timer=setInterval(
          function(){
            //如果超过宽度停止定时器
            if(that.count>=600){
              clearInterval(this.timer);
            }
            that.count+=that.w
            if(that.s>=60){
            	that.m+=1;
            	that.s=0
            }else{
            	that.s+=1;
            }
          }
        ,1000);
        },
        //当音乐准备完毕开始播放后
        getDuration(){
          this.zero()
          this.playflag=true
          var that=this
          clearInterval(that.timer)
          //对缓冲好的歌曲时间进行格式化duration_time
          that.duration=parseInt(this.$refs.au.duration)
          var dm=parseInt(that.duration/60)<10?'0'+parseInt(that.duration/60):parseInt(that.duration/60)
          var ds=that.duration%60<10?'0'+that.duration%60:that.duration%60
          this.duration_time=dm+":"+ds
          //每秒需要进行的赠宽
          that.w=that.width/that.duration
          console.log("数据中的秒数"+that.w)
          this.timer=setInterval(
            function(){
              //如果超过宽度停止定时器
              if(that.count>=600){
                clearInterval(that.timer);
                console.log('已经清楚')
                console.log(that.count)
              }
              that.count+=that.w
              // console.log(that.count)
              if(that.s>=60){
              	that.m+=1;
              	that.s=0
              }else{
              	that.s+=1;
              }
              // console.log(that.m+":"+that.s)
            }
          ,1000)
        },
        //滑动操作
        overlay(){
          console.log('滑动操作开始了')
        },
        //将进度条的时间归零
        zero(){
          this.m=0
          this.s=0
          this.count=0
          this.stop_m=0
          this.stop_s=0
        },
        //开始暂停
        reset(){
          this.stop_s=this.s
          this.stop_m=this.m
          clearInterval(stop)
        },
        //当音乐的按钮被点击
        showvolume(){
          this.showvolumeo==true?this.showvolumeo=false:this.showvolumeo=true
        },
        //当播放列表的按钮被单击
        showplaylist(){
          this.showpalylistbtn==true?this.showpalylistbtn=false:this.showpalylistbtn=true
        },
        //当音量的盒子被按下
        mdown(){
          this.volumeflag=true
        },
        //当滑动的时候离开
        mdownleave(){
          this.volumeflag=false
        },
        //当鼠标开始移动
        volumemove(e){
          if(!this.volumeflag==true)return;
          this.$refs.au.volume=(100-this.volumemovetop)/100
          console.log("当前音量等于"+this.$refs.au.volume)
          console.log(e.offsetY)
          this.volumemovetop=e.offsetY
        }
        //歌曲列表


      },


    mounted(){
        var that=this;
        // musicid=item.id
        that.musicid=that.item.id
      },
      watch:{

        item:function(){
          this.isId(this.item.id)
          console.log("播放的id"+this.item.id)
          console.log(this.queryindex());
          if(!this.listflag){
            console.log(this.listflag)
            this.addlist()
          }
          //获取当前播放列表中的id
          // this.listindex= this.orderPlay()
         this.changemusic();

          this.listflag=false
          this.zero()
          this.show=true
          this.timer
          clearInterval(this.timer)
          // clearInterval(stop)
          // this.stop()
        },


      }

  }

</script>

<style>
  .bar_audio{
    width: 1500px;
    margin: 0 auto;
  }
  .bar_audio *{
    /* border: 1px solid #000000; */
  }
  .boxa{
  	width: 600px;
  	height: 70px;
  	/* margin: 0 auto; */
  	position: relative;
    margin-left: 60px;
  }
  .progress_bar{
  	/* background-color: #cfcfcf; */
  	width: 100%;
  	height: 70px;
  	display: flex;
  	align-items: center;
    flex: 3;
    overflow: hidden;
  }
  .bar_inner{
  	position: relative;
  	background-color: azure;
  	width: 100%;
  	height: 3px;
  }
  .bar_inner:hover{
  	cursor: pointer;
  }
  .progress-btn{
  	width: 10px;
  	height: 10px;
  	background-color: aquamarine;
  	position: absolute;
  	top: -4px;
  	left: 0;
  	border-radius: 50%;
  	z-index: 10;
  }
  .progress{
  	position: absolute;
  	height: 3px;
  	/* width: 40px; */
  	background-color: aquamarine;
  }
  .progress_left{
      position: absolute;
      left: -60px;
      top: 25px;
  }
  .progress_right{
    position: absolute;
    right: -60px;
    top: 25px;
  }

  .overlay{
  	width: 100%;
  	height: 100%;
  	background-color: rgba(255,255,255,0.1);
  	position: absolute;
  	left: 0;
  	top: 0;
    cursor: pointer;
  }


  .bar_audio{
    display: flex;
  }
  .play{
    margin: 0 20px;
    padding-top: 20px;
    box-sizing: border-box;
    width: 100px;
    height: 70px;
  }
  .play img{
    width: 30px;
    height: 30px;
  }
  .audio_icon img{
    width: 60px;
    height: 60px;
  }
  .audio_name{
    width: 150px;
    margin: 0 20px;
    height: 70px;
    overflow: hidden;
    padding: 0 20px;
  }
  .audio_name *{
    width: 100%;
    margin-top: 10px;
  }
  .audio_volume img{
    width: 20px;
    height: 20px;
  }
  .audio_volume{
   display: flex;
   margin-left: 80px;
  }
  .audio_volume span{
    flex: 1;
    padding: 25px 10px;
    position: relative;
  }
/* 音量按钮 */
  .volume{
    position: absolute;
    background-color: rgba(255,255,255,255);
    width: 40px;
    height: 100px;
    left: 0;
    top: -100px;
    display: flex;
  }
  .volume_bar{
    margin: 0 auto;
    background-color: #dcdcdc;
    width: 3px;
    height: 100px;
  }
  .volume_inner{

     height: 100px;
     width: 3px;
     position: relative;
     background-color: aquamarine;
  }
  .volume-btn{
    width: 10px;
    height: 10px;
    border-radius: 100%;
    background-color: aquamarine;
    position: absolute;
    left: -3px;
    bottom: 0;
  }
  .volume_pre{
    width: 3px;
    background-color: azure;
  }
  /* 播放列表 */
  .playlist{
    position: absolute;
    width: 400px;
    height: 400px;
    left: -200px;
    top: -400px;
    background-color: white;
    box-shadow: 0px 0px 1px 1px  #dddddd;
    z-index: 9999;
  }
  .posilayout{
    position: relative;
  }
  .playlist ul{
    padding: 20px 0;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    overflow: auto;
    padding: 0 20px;
    box-sizing: border-box;

  }
  .playlist li{
    width: 100%;
    height: 40px;
    border-bottom:1px solid #adff42;
    text-align: left;
    text-indent: 20px;
    line-height: 40px;
    list-style: none;
  }
  .changecolor{
    color: #ffe0f7;
  }

</style>



