import Vue from 'vue'
import Router from 'vue-router'
import fg from '../components/private/newsong/newsong.vue'
import recommend from '../components/private/recommend/recommend.vue'

import app from '../App.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'app',
      component: app
    },
    {
      path:'/fg',
      component:fg
    },
    {
      path:'/recommend',
      component:recommend
    }
  ]
})



