<template>
  <div class="singer_box">
      <div class="singer_com">
        <h3>推荐歌手</h3>
      </div>
      <div class="singer_box_list">
        <ul>
          <li v-for="item in singer">
            <div class="singer_icon"><img v-bind:src="item.picUrl" alt=""></div>
            <div><h5>{{item.name}}</h5></div>
            <div><p>单曲数{{item.musicSize}}</p></div>
          </li>
        </ul>
      </div>
  </div>
</template>

<script>
  import singer from '../data/singer.js'
  export default{
    data(){
      return{
        singer:singer.artists
      }
    }
  }
  console.log(singer.artists)
</script>

<style>
  .singer_icon img{
    width: 100px;
    height: 90px;
    border-radius: 100%;
  }
  .singer_box{
    width: 1300px;
    height: auto;
    margin: 0 auto;
  }
  .singer_box_list {

  }
  .singer_box_list ul {
      display: flex;
      flex-wrap: wrap;

  }
  .singer_box_list li {
      width: 130px;
      height: 180px;
      list-style: none;
      text-align: center;
  }
  .singer_box_list p {
     width: 100%
  }
  .singer_com{
    text-indent: 20px;
    height: 70px;
    line-height: 70px;
  }
  .singer_icon p{
    font-size: 12px;
    display: block;
    height: 40px;
    line-height: 40px;
  }

</style>



